

import React, { Component, useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Drawer, Modal } from 'antd';
import Approved from './approved/approved'
import NewUsers from './new-users/new-users'
import Pending from './pending/pending'
import Profile from './profile/profile'
import './users.css'
//api
import * as actionType from '../../../../store/actions/action-type'
import { GetToken } from '../../../../helpers/token'
import getUsers from '../../../../services/api/get-users'
import getUserProfile from '../../../../services/api/get-user-detail'
import userApproval from '../../../../services/api/approve-user'

const Users = (props) => {

    const [visible, setVisible] = useState(false);
    const [token, setToken] = useState(GetToken());

    const success = () => {
        Modal.success({
            title: 'Successful',
            content: 'User has been approved :).',
        });
    }

    const callback = (data) => {
        if (data.status === 200) {
            props.getAllUsers(data.data);
            props.dispatchUpdateFirstRunUsers(false);
        } else {
            console.log(data.response)
        }
    }

    useEffect(() => {
        if (props.firstRunUsers) {
            getUsers(callback, token);
        }
    },[])

    const profileCallback = (data) => {
        if (data.status === 200) {
            props.dispatchProfile(data.data);
            setVisible(true);
        } else {
            console.log(data.response)
            // setshowLoade(false)
        }
    }

    const openDrawer = (id) => {
        getUserProfile(profileCallback, id, token)
    }

    const approvalCallback = (data) => {
        if (data.status === 200) {
            props.dispatchApproval(data.data)
            success();
        } else {
            console.log(data.response);
        }
    }

    const approveUser = (id) => {
        userApproval(approvalCallback, id, token);
    }

    const onClose = () => {
        setVisible(false);
    };


    return (
        <div className="users-container">
            <Drawer
                width={640}
                placement="right"
                maskClosable={false}
                onClose={onClose}
                visible={visible}
            >
                <Profile profile={props.profile} />
            </Drawer>
            {
                props.users !== null && props.users !== undefined && (
                    <div className="table-list">
                        <NewUsers users={props.users} openDrawer={openDrawer} classValue="table" />
                        <Pending users={props.users} openDrawer={openDrawer} approveUser={approveUser} classValue="table" />
                        <Approved users={props.users} openDrawer={openDrawer} classValue="table" />
                    </div>
                )
            }
        </div>
    )

}

function mapStateToProps(state) {
    return {
        firstRunUsers: state.Users.firstRun,
        users: state.Users.users,
        profile: state.Users.profile
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getAllUsers: (data) => {
            dispatch({
                type: actionType.USERS,
                value: data
            })
        },
        dispatchProfile: (data) => {
            dispatch({
                type: actionType.USER_PROFILE,
                value: data
            })
        },
        dispatchApproval: (data) => {
            dispatch({
                type: actionType.APPROVE_USER,
                value: data
            })
        },
        dispatchUpdateFirstRunUsers: (data) => {
            dispatch({
                type: actionType.UPDATE_FIRST_RUN_USERS,
                value: data
            })
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Users)